import PressSearch from 'press-ui/press-search/press-search.vue';

export default {
  i18n: {
    'zh-CN': {
      label: '地址',
      disabled: '禁用搜索框',
      inputAlign: '搜索框内容对齐',
      background: '自定义背景色',
      placeholder: '请输入搜索关键词',
      customButton: '自定义按钮',
      listenToEvents: '事件监听',
    },
    'en-US': {
      label: 'Address',
      disabled: 'Disabled',
      inputAlign: 'Input Align',
      background: 'Custom Background Color',
      placeholder: 'Placeholder',
      customButton: 'Custom Action Button',
      listenToEvents: 'Listen to Events',
    },
  },

  components: {
    PressSearch,
  },
  data() {
    return {
      value: '',
    };
  },
  methods: {
    onChange(val) {
      if (typeof val === 'string') {
        this.onGTip(`${val}`);
      }
    },
    onCancel() {
      this.onGTip('cancel');
    },
    onSearch() {
      this.onGTip('search');
    },
    onClick() {
      this.onGTip('click');
    },
  },
};
